body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

h1 {
  font-size: 2.2em; }

p {
  font-size: 12px; }

.title-section {
  background-image: url(/croatia-see-meets-uk/static/media/citiy-bw_op.540fa401.jpg);
  background-size: cover;
  padding-bottom: 50px; }

.why-participate {
  background-image: url(/croatia-see-meets-uk/static/media/coworking_bw_op.3a900651.jpg);
  background-size: cover;
  padding-bottom: 50px; }

.encode-program {
  background-image: url(/croatia-see-meets-uk/static/media/coworking_space_bw_op.1b312cf1.jpg);
  background-size: cover;
  padding-bottom: 50px; }

.about-bccc {
  background-image: url(/croatia-see-meets-uk/static/media/gerkhin-bw_op.6fe98b02.jpg);
  background-size: cover;
  padding-bottom: 50px; }

.about-the-event {
  padding-bottom: 50px; }

.agenda {
  padding-bottom: 50px; }

.how-to-apply {
  padding-bottom: 50px; }

.media {
  padding-bottom: 50px; }

.no-margin {
  margin: 0; }

.header-menu {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent; }

.aqua-header__container {
  padding-top: 8px;
  padding-bottom: 8px; }
  .aqua-header__container--small {
    width: 60%;
    padding-top: 8px;
    padding-bottom: 8px; }
  .aqua-header__container--spacing {
    padding-top: 40px;
    padding-bottom: 20px; }
  .aqua-header__container--spacingTop {
    padding-top: 20px; }

.aqua-header__text {
  background-color: #00ffff;
  color: black;
  box-shadow: 6px 0 0 #00ffff, -6px 0 0 #00ffff;
  line-height: 16px;
  padding: 1px 0 1px 0; }

.titles {
  color: white;
  padding-left: 30px;
  padding-right: 30px; }

.content {
  padding-left: 25px;
  padding-right: 21px; }

.p-size {
  font-size: 12px; }

.p-contrast {
  box-shadow: 6px 0 0 white, -6px 0 0 white;
  line-height: 16px;
  padding: 1px 0 1px 0;
  background-color: white;
  color: black; }

.about-the-event {
  background-color: white; }
  .about-the-event .content {
    padding-right: 21px; }

.purple-header__container--spacing {
  padding-bottom: 20px; }

.purple-header__container--spacingTop {
  padding-top: 20px; }

.purple-header__text {
  font-weight: bold;
  color: white;
  background-color: #715dff;
  box-shadow: 6px 0 0 #715dff, -6px 0 0 #715dff;
  line-height: 16px;
  padding: 1px 0 1px 0; }

.separator {
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
  height: 1px;
  background-color: #cccccc; }

.purple {
  color: #715dff; }
  .purple--underline {
    color: #715dff;
    text-decoration: underline; }
  .purple--title {
    color: #715dff;
    font-size: 14px;
    font-weight: bold;
    padding-top: 8px;
    display: inline-block; }

.aqua {
  color: #00ffff; }
  .aqua--underline {
    color: #00ffff;
    text-decoration: underline; }

.why-participate__container--square {
  padding-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center; }

.box-purple {
  width: 260px;
  color: white;
  background-color: #715dff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .box-purple__header {
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center; }
  .box-purple__text {
    font-size: 12px; }
    .box-purple__text-container {
      padding-left: 16px;
      padding-right: 16px;
      padding-bottom: 16px; }

.paddingTop--small {
  padding-top: 8px; }

.bullet__container {
  display: flex;
  padding-top: 8px; }

.bullet__point {
  padding-top: 2px;
  color: #715dff;
  font-size: 10px;
  padding-right: 4px; }

.bullet__text {
  color: black;
  font-size: 12px; }

.footer {
  padding: 16px 0 0 0;
  background-color: #cccccc;
  font-size: 12px;
  color: #525659;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.terms-privacy {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around; }

.copyrights {
  width: 80%;
  font-size: 12px;
  text-align: center; }
  .copyrights__container {
    display: flex;
    justify-content: center; }

